<template>
  <div id="app">
    <div v-if="selected" style="padding-top:10px; width: 100%;">
      You have selected <code>{{selected.name}}, navigating to: {{selected.url}}</code>
    </div>
    <div class="autosuggest-container" v-show="visible">
      <vue-autosuggest
              v-model="query"
              :suggestions="filteredOptions"
              @focus="focusMe"
              @click="clickHandler"
              @input="onInputChange"
              @selected="onSelected"
              :get-suggestion-value="getSuggestionValue"
              :input-props="{id:'autosuggest__input', placeholder:'Search for anything!'}"
              v-focus>
        <div slot-scope="{suggestion}" style="display: flex; align-items: center;">
          <img :style="{ display: 'flex', width: '25px', height: '25px', borderRadius: '15px', marginRight: '10px'}" :src="suggestion.item.avatar" />
          <div style="{ display: 'flex', color: 'navyblue'}">{{suggestion.item.name}}</div>
        </div>
      </vue-autosuggest>
    </div>
  </div>
</template>

<script>
import { VueAutosuggest } from "vue-autosuggest";

export default {
  created: function () {
    window.addEventListener('keyup', this.keyPressed)
  },
  beforeDestroy: function () {
    window.removeEventListener('keyup', this.keyPressed)
  },
  data() {
    return {
      visible: false,
      query: "",
      selected: "",
      suggestions: [
        {
          data: [
            {
              id: 1,
              name: "Transactions",
              url: "/transactions",
              avatar: "https://upload.wikimedia.org/wikipedia/en/thumb/4/4e/Elijah_Wood_as_Frodo_Baggins.png/220px-Elijah_Wood_as_Frodo_Baggins.png"
            },
            {
              id: 2,
              name: "Subscribers",
              url: "/subscribers",
              avatar: "https://upload.wikimedia.org/wikipedia/en/thumb/7/7b/Sean_Astin_as_Samwise_Gamgee.png/200px-Sean_Astin_as_Samwise_Gamgee.png"
            },
            {
              id: 3,
              name: "Subscription Plans",
              url: "/plans",
              avatar: "https://upload.wikimedia.org/wikipedia/en/thumb/e/e9/Gandalf600ppx.jpg/220px-Gandalf600ppx.jpg"
            },
            {
              id: 4,
              name: "Products",
              url: "/products",
              avatar: "https://upload.wikimedia.org/wikipedia/en/thumb/3/35/Aragorn300ppx.png/150px-Aragorn300ppx.png"
            }
          ],
          default: [
            {id: 1, name: "Search Transactions for \"" + this.query + "\"", url: "/transactions?q=" + this.query},
            {id: 2, name: "Search Subscribers for \"" + this.query + "\"", url: "/subscribers?q=" + this.query},
            {id: 3, name: "Search Vendors for \"" + this.query + "\"", url: "/subscribers?q=" + this.query}
          ]
        }
      ]
    };
  },
  computed: {
    defaultOptions() {
      return [
        {id: 1, name: "Search Transactions for \"" + this.query + "\"", url: "/transactions?q=" + this.query},
        {id: 2, name: "Search Subscribers for \"" + this.query + "\"", url: "/subscribers?q=" + this.query},
        {id: 3, name: "Search Vendors for \"" + this.query + "\"", url: "/subscribers?q=" + this.query}
      ]
    },
    filteredOptions() {
      let filteredData = this.suggestions[0].data.filter(option => {
        try {
          return option.name.toLowerCase().indexOf(this.query.toLowerCase()) > -1
        } catch (err) {
          return this.suggestions[0].data
        }
      })
      if (this.query.length > 0) {
        console.log(this.defaultOptions)
        for (let i = 0; i < this.defaultOptions.length; i++) {
          filteredData.push(this.defaultOptions[i]);
        }
      }

      return [
        {
          data: filteredData
        }
      ];
    }
  },
  methods: {
    clickHandler() {
      // event fired when clicking on the input
    },
    onSelected(item) {
      this.selected = item.item;
    },
    onInputChange(text) {
      // event fired when the input changes
      console.log(text)
    },
    keyPressed(e) {
      console.log(e);
      if (e.key == '§') {
        this.visible = true;
      }
      if (e.key == 'Escape') {
        this.visible = false;
      }
    },
    getSuggestionValue(suggestion) {
      return suggestion.item.name;
    },
    focusMe(e) {
      console.log(e) // FocusEvent
    }
  },
  components: {
    VueAutosuggest
  }
}
</script>

