import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false
Vue.directive('focus', {
  // When the bound element is inserted into the DOM...
  update: function (el) {
    // Focus the element
    Vue.nextTick(() => {
      el.querySelector("input").click();
      el.querySelector("input").focus();
    });
  }
})

new Vue({
  render: h => h(App),
}).$mount('#app')
